<template>
  <transition
    name="fade"
    v-show="sell !== null && showGraph !== false && tickers.length > 0"
  >
    <div class="relative graph">
      <h3 class="text-sm leading-6 font-medium text-gray-900 my-4">
        {{ sell ? sell.name : "" }} - USD
      </h3>
      <div class="flex graph-body">
        <div class="num-items">
          <p>{{ dataMax }}</p>
          <p>{{ dataMin !== dataMax ? dataMin : "-" }}</p>
        </div>
        <div
          ref="graphLines"
          class="graph-lines flex items-end border-gray-600 border-b border-l h-64"
        >
          <div
            v-for="(h, key) in graphHeights"
            :key="key"
            :style="{ height: h === 0 ? 2 + '%' : h + '%' }"
            class="graph-line bg-green-700 border w-3"
          ></div>
        </div>
      </div>

      <button
        @click="changeShowGraph(), nullAllData()"
        type="button"
        class="absolute top-0 right-0"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          xmlns:svgjs="http://svgjs.com/svgjs"
          version="1.1"
          width="30"
          height="30"
          x="0"
          y="0"
          viewBox="0 0 511.76 511.76"
          style="enable-background: new 0 0 512 512"
          xml:space="preserve"
        >
          <g>
            <path
              d="M436.896,74.869c-99.84-99.819-262.208-99.819-362.048,0c-99.797,99.819-99.797,262.229,0,362.048    c49.92,49.899,115.477,74.837,181.035,74.837s131.093-24.939,181.013-74.837C536.715,337.099,536.715,174.688,436.896,74.869z     M361.461,331.317c8.341,8.341,8.341,21.824,0,30.165c-4.16,4.16-9.621,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    l-75.413-75.435l-75.392,75.413c-4.181,4.16-9.643,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    c-8.341-8.341-8.341-21.845,0-30.165l75.392-75.413l-75.413-75.413c-8.341-8.341-8.341-21.845,0-30.165    c8.32-8.341,21.824-8.341,30.165,0l75.413,75.413l75.413-75.413c8.341-8.341,21.824-8.341,30.165,0    c8.341,8.32,8.341,21.824,0,30.165l-75.413,75.413L361.461,331.317z"
              fill="#15803D"
              data-original="#000000"
            ></path>
          </g>
        </svg>
      </button>
      <button
        @click="changeEndGraphBtn"
        v-show="showEndBtn"
        class="end-graph wave-button"
      >
        {{ watchButtonText }}
      </button>
    </div>
  </transition>
  <transition name="fade" v-show="tickers.length && !showGraph">
    <div class="block text-sm text-center font-medium text-gray-700 mt-4">
      Click on the card to see the price change graph
    </div>
  </transition>
</template>

<script>
import "@/styles/button-effect.scss";
import { showButtonClickWave } from "@/js/button-effect";

export default {
  name: "MyComponent",
  components: {},
  directives: {},
  inheritAttrs: true,
  props: {
    showGraph: { type: Boolean, required: true, default: false },
    sell: { type: Object, required: false, default: null },
    tickers: { type: Array, required: false, default: () => [] },
  },
  emits: {
    newShowGraph: { type: Boolean },
  },
  setup() {},
  data() {
    return {
      showEndBtn: false,
      dataMax: "-",
      dataMin: "-",
      graphPrice: [],
      graphHeights: [],

      watchGraph: false,
      watchButtonText: "Follow",
      intervalForScroll: null,
    };
  },
  computed: {
  },
  watch: {
    sell(newValue) {
      this.graph(newValue);
      this.showEndGraphBtn();
      this.scrollEndGraph();
    },
  },
  methods: {
    changeShowGraph() {
      this.$emit("newShowGraph", false);
    },
    changeEndGraphBtn() {
      this.watchGraph = !this.watchGraph;

      if (this.watchGraph) {
        this.watchButtonText = "Following";

        this.scrollEndGraph();
      } else {
        this.watchButtonText = "Follow";
      }
    },
    showEndGraphBtn() {
      const graphWrapper = this.$refs.graphLines;

      if (graphWrapper.scrollWidth > graphWrapper.clientWidth) {
        this.showEndBtn = true;
      } else {
        this.showEndBtn = false;
      }
    },
    scrollEndGraph() {
      if (this.watchGraph) {
        this.$nextTick(() => {
          const graphWrapper = this.$refs.graphLines;
          const maxScroll = graphWrapper.scrollWidth - graphWrapper.clientWidth;
          graphWrapper.scrollLeft = maxScroll;
        });
      }
    },
    graph(sell) {
      if (this.sell !== null && sell === this.sell) {
        this.graphPrice.push(sell.price);
        this.dataMin = Math.min(...this.graphPrice);
        this.dataMax = Math.max(...this.graphPrice);
        this.graphHeights = this.graphPrice.map((price) => {
          if (this.dataMax === this.dataMin) {
            return 100;
          } else {
            return Math.round(
              ((price - this.dataMin) / (this.dataMax - this.dataMin)) * 100
            );
          }
        });
      }
    },
    nullAllData() {
      this.dataMin = "-";
      this.dataMax = "-";
      this.graphPrice = [];
      this.graphHeights = [];

      clearInterval(this.setInterval);
    },
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    showButtonClickWave();
  },
  beforeUpdate() {},
  updated() {},
  beforeUnmount() {},
  unmounted() {},
  errorCaptured() {},
};
</script>

