<template>
  <div class="page-container">
    <div class="page-back"></div>
    <div class="page-filter"></div>
  </div>
</template>

<script>
export default {
  name: "MyComponent",
  components: {},
  directives: {},
  inheritAttrs: true,
  props: {},
  emits: {},
  setup() {},
  data() {
    return {};
  },
  computed: {},
  watch: {},
  methods: {
    moveBackground() {
      if (window.innerWidth >= 1025) {
        var pageBack = document.querySelector(".page-back");

        document.addEventListener("mousemove", function (event) {
          var moveX = (window.innerWidth / 2 - event.pageX) * 0.1;
          var moveY = (window.innerHeight / 2 - event.pageY) * 0.1;

          pageBack.style.marginLeft = moveX + "px";
          pageBack.style.marginTop = moveY + "px";
        });
      }
    },
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    this.moveBackground();
  },
  beforeUpdate() {},
  updated() {},
  beforeUnmount() {},
  unmounted() {},
  errorCaptured() {},
};
</script>

<style lang="scss">
.page-container {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #fff;
  z-index: -1;
}
.page-filter {
  z-index: 2;
  width: 120%;
  height: 120%;
  left: -10%;
  top: -10%;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.9);
}

.page-back {
  width: 120%;
  height: 120%;
  left: -10%;
  top: -10%;
  position: absolute;
  background-image: url("../../public/cripto.jpg");
  background-size: cover;
  background-position: center;
  object-fit: cover;

  z-index: 1;
}
</style>
