<template>
  <div class="container mx-auto flex flex-col items-center p-4 mt-5 mb-10">
    <!-- <div class="fixed w-100 h-100 opacity-80 bg-purple-800 inset-0 z-50 flex items-center justify-center">
    <svg class="animate-spin -ml-1 mr-3 h-12 w-12 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
      <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
      <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
    </svg>
    </div> -->
    <BackgroundComponent />

    <PopupComponent ref="popupDelete">
      <template #title>
        <h3 class="title">Attention</h3>
      </template>
      <template #text>
        <p class="text">
          Are you sure for delete
          <span>{{ this.tickerNameToPopup }}</span> ?
        </p>
      </template>
      <template #buttons>
        <button
          @click="
            this.$refs.popupDelete.togglePopup();
            this.$refs.TickersComponent.remove(tickerKey, tickerEvent);
          "
          class="button"
          type="button"
        >
          <span class="text">Yes</span>
        </button>
        <button
          @click="this.$refs.popupDelete.togglePopup()"
          class="button"
          type="button"
        >
          <span class="text">No</span>
        </button>
      </template>
    </PopupComponent>

    <PopupComponent ref="popupShow">
      <template #text>
        <p class="text">
          Are you sure for delete <span>this</span> criptocarency ?
        </p>
      </template>
    </PopupComponent>

    <div class="container">
      <AddTickersComponent
        ref="AddTickersComponent"
        :tickers="tickers"
        :propUpdateTime="updateTime"
        @newObjForTickers="addNewObjForTickers"
        @newRespDataUSD="addNewRespDataUSD"
        @newIntervalId="addNewIntervalId"
      />

      <TickersComponent
        ref="TickersComponent"
        :tickers="tickers"
        :sell="sell"
        :showGraph="showGraph"
        @tickerId="removeObjInTickers"
        @newShowGraph="addNewShowGraph"
        @newSell="setSell"
        @newObjForSell="setObjForSell"
        @newGraphData="clearGraphData"
        @tickerForSubscribe="subscribeToTickerPrice"
        @newTickers="setTickers"
        @changeStatus="newPopupStatus"
        @getTickerData="setTickerDataToPopup"
      />

      <GraphComponent
        ref="GrapComponent"
        :showGraph="showGraph"
        :sell="sell"
        :tickers="tickers"
        @newShowGraph="addNewShowGraph"
      />
    </div>
  </div>
</template>

<script>
import BackgroundComponent from "./components/BackgroundComponent.vue";
import AddTickersComponent from "./components/AddTickersComponent.vue";
import TickersComponent from "./components/TickersComponent.vue";
import GraphComponent from "./components/GraphComponent.vue";
import PopupComponent from "./components/PopupComponent.vue";

// import PaginationComponent from "./components/PaginationComponent.vue";

import "@/assets/tailwind.css";

export default {
  components: {
    GraphComponent,
    BackgroundComponent,
    AddTickersComponent,
    TickersComponent,
    PopupComponent,
    // PaginationComponent,
  },
  data() {
    return {
      updateTime: 120000000,

      tickerNameToPopup: null,
      tickerKey: null,
      tickerEvent: null,

      // Subscribe
      intervalId: null,
      intervals: [],

      // Tickers
      tickers: [],

      // Graph
      sell: null,
      showGraph: false,
    };
  },
  computed: {},
  watch: {},
  methods: {
    newPopupStatus() {
      this.$refs.popupDelete.togglePopup();
    },
    setTickerDataToPopup(tickerName, tickerKey, $event) {
      this.tickerNameToPopup = tickerName;
      this.tickerKey = tickerKey;
      this.tickerEvent = $event;
    },
    addNewObjForTickers(newObjForTickers) {
      this.tickers.push(newObjForTickers);
    },
    addNewRespDataUSD(newRespDataUSD, obj) {
      const foundTicker = this.tickers.find((t) => t.name === obj.name);
      if (foundTicker) {
        const updatedTickers = this.tickers.map((ticker) =>
          ticker.name === obj.name
            ? { ...ticker, price: newRespDataUSD }
            : ticker
        );

        this.tickers = updatedTickers;

        this.$refs.TickersComponent.setStorageTickets();
      }
    },
    addNewShowGraph(newShowGraph) {
      this.showGraph = newShowGraph;
    },
    addNewIntervalId(newShowGraph) {
      this.intervals.push(newShowGraph);
    },
    removeObjInTickers(tickerId) {
      this.tickers.splice(tickerId, 1);
      const intervalId = this.intervals.splice(tickerId, 1)[0];
      clearInterval(intervalId);
    },
    setSell(value) {
      this.sell = value;
    },
    setObjForSell(value) {
      this.objForSell = value;
    },
    clearGraphData() {
      this.$refs.GrapComponent.nullAllData();
    },
    subscribeToTickerPrice(ticker) {
      this.$refs.AddTickersComponent.subscribeToPrice(ticker);
    },
    setTickers(array) {
      this.tickers = array;
    },
    changeStatus() {
      this.$refs.popupDelete.showPopup();
    },
  },
  mounted() {},
};
</script>

