<template>
  <section class="flex items-center justify-center flex-col">
    <div class="flex content-center place-content-center max-w-xs w-full">
      <div class="max-w-sm w-full">
        <label
          for="wallet"
          class="block text-sm text-center font-medium text-gray-700"
          >Enter the name of the cryptocurrency</label
        >
        <div class="mt-1 relative rounded-md shadow-md">
          <input
            v-model="inputAddValue"
            v-on:keydown.enter="add"
            @input="
              showCoinlist();
              checkCoinListLenght();
            "
            type="text"
            name="wallet"
            id="wallet"
            class="block w-full pr-10 border-gray-300 text-gray-900 focus:outline-none focus:ring-gray-500 focus:border-green-700 sm:text-sm rounded-md mb-1"
            placeholder="For example BTC"
          />
          <button
            type="button"
            class="absolute top-2 right-2"
            @click="clearInputAddValue"
            v-show="inputAddValue !== ''"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
              xmlns:svgjs="http://svgjs.com/svgjs"
              version="1.1"
              width="20"
              height="20"
              x="0"
              y="0"
              viewBox="0 0 511.76 511.76"
              style="enable-background: new 0 0 512 512"
              xml:space="preserve"
            >
              <g>
                <path
                  d="M436.896,74.869c-99.84-99.819-262.208-99.819-362.048,0c-99.797,99.819-99.797,262.229,0,362.048    c49.92,49.899,115.477,74.837,181.035,74.837s131.093-24.939,181.013-74.837C536.715,337.099,536.715,174.688,436.896,74.869z     M361.461,331.317c8.341,8.341,8.341,21.824,0,30.165c-4.16,4.16-9.621,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    l-75.413-75.435l-75.392,75.413c-4.181,4.16-9.643,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    c-8.341-8.341-8.341-21.845,0-30.165l75.392-75.413l-75.413-75.413c-8.341-8.341-8.341-21.845,0-30.165    c8.32-8.341,21.824-8.341,30.165,0l75.413,75.413l75.413-75.413c8.341-8.341,21.824-8.341,30.165,0    c8.341,8.32,8.341,21.824,0,30.165l-75.413,75.413L361.461,331.317z"
                  fill="#15803D"
                  data-original="#000000"
                ></path>
              </g>
            </svg>
          </button>
        </div>
        <transition name="fade">
          <div class="flex bg-white shadow-md p-1 rounded-md">
            <span
              v-for="(itemItem, key) in showCoinlist()"
              :key="key"
              @click="choice(key)"
              class="wave-button flex w-full justify-center items-center px-2 py-1 m-1 rounded-md text-xs font-medium bg-green-700 text-white cursor-pointer hover:bg-green-800"
            >
              {{ itemItem.symbol }}
            </span>
            <span
              v-show="coinListLength"
              class="flex w-full justify-center items-center px-2 py-1 m-1 rounded-md text-xs font-medium text-black"
            >
              Nothing found
            </span>
          </div>
        </transition>
        <div class="error-messages mt-1 text-sm text-red-600 text-center">
          {{ showMessage ? message : "" }}
        </div>
      </div>
    </div>
    <button
      @click="add"
      type="button"
      class="wave-button mt-2 mb-6 max-w-fit inline-flex items-center py-2 px-14 border border-transparent shadow-sm text-sm leading-4 font-medium rounded-md text-white bg-green-700 hover:bg-green-800 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
    >
      <svg
        class="-ml-0.5 mr-2 h-6 w-6"
        xmlns="http://www.w3.org/2000/svg"
        width="30"
        height="30"
        viewBox="0 0 24 24"
        fill="#ffffff"
      >
        <path
          d="M13 7h-2v4H7v2h4v4h2v-4h4v-2h-4V7zm-1-5C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z"
        ></path>
      </svg>
      ADD
    </button>
  </section>
</template>

<script>
import { getCoinList } from "@/api/get-coin-list";
import { getCoinPrice } from "@/api/get-coin-price";

import "@/styles/button-effect.scss";
import { showButtonClickWave } from "@/js/button-effect";
export default {
  components: {},
  props: {
    tickers: { type: Array, required: true, default: () => [] },
    propUpdateTime: { type: Number, required: true },
  },
  emits: {
    newIntervalId: { type: Object },
    newObjForTickers: { type: Object },
    newRespDataUSD: { type: Number },
  },
  setup() {},
  data() {
    return {
      updateTime: this.propUpdateTime,
      coinList: [],
      inputAddValue: "",
      message: "",
      showMessage: false,
      coinListLength: false,
      customCoinList: [
        { symbol: "BTC", fullName: "Bitcoin (BTC)" },
        { symbol: "ETH", fullName: "Ethereum (ETH)" },
        { symbol: "XLM", fullName: "Stellar (XLM)" },
        { symbol: "DASH", fullName: "Dash (DASH)" },
      ],
    };
  },
  computed: {},
  watch: {},
  methods: {
    async setCoinList() {
      const coinListData = await getCoinList();

      const array = Object.values(coinListData.Data);

      for (const object of array) {
        const objectSymbol = object.Symbol;
        const objectFullName = object.FullName;

        const objectData = {
          symbol: objectSymbol,
          fullName: objectFullName,
        };

        this.coinList.push(objectData);
      }
    },
    subscribeToPrice(obj) {
      const intervalId = setInterval(() => this.getPrice(obj), 5000);
      this.$emit("newIntervalId", intervalId);
    },
    async getPrice(obj) {
      if (this.tickers.length > 0) {
        const respData = await getCoinPrice(obj);
        this.$emit("newRespDataUSD", respData.USD, obj);
      }
    },
    showMessageText(message) {
      if (message) {
        this.message = message;
        this.showMessage = true;

        setTimeout(() => {
          this.showMessage = false;
        }, 5000);
      } else {
        this.message = "";
        this.showMessage = false;
      }
    },
    choice(key) {
      const array = this.showCoinlist();
      const currentObj = array[key].symbol;

      const obj = {
        name: array[key].symbol,
        fullName: array[key].fullName,
        price: "-",
      };

      if (!this.tickers.some((obj) => obj.name === currentObj)) {
        this.$emit("newObjForTickers", obj);
        this.getPrice(obj);
        this.subscribeToPrice(obj);
        this.showMessageText();
      } else {
        this.showMessageText("Cryptocurrency has already been added");
      }
    },
    add() {
      const inputAddValue = this.inputAddValue.toUpperCase();

      if (
        this.coinList.find((obj) => obj.symbol === inputAddValue) ||
        this.coinList.find((obj) => obj.fullName === inputAddValue)
      ) {
        if (!this.tickers.some((obj) => obj.name === inputAddValue)) {
          const fullNameCoin = this.coinList.find(
            (obj) => obj.symbol === inputAddValue
          );
          const obj = {
            name: this.inputAddValue.toUpperCase(),
            fullName: fullNameCoin.fullName,
            price: "-",
          };
          this.$emit("newObjForTickers", obj);
          this.getPrice(obj);
          this.subscribeToPrice(obj);
          this.showMessageText();
        } else {
          this.showMessageText("Cryptocurrency has already been added");
        }
      } else {
        if (inputAddValue === "") {
          this.showMessageText("The input field cannot be empty");
        } else {
          this.showMessageText("This coin does not exist");
        }
      }
    },
    clearInputAddValue() {
      this.inputAddValue = "";
      this.checkCoinListLenght();
    },
    checkCoinListLenght() {
      if (this.showCoinlist().length === 0) {
        this.coinListLength = true;
      } else {
        this.coinListLength = false;
      }
    },
    showCoinlist() {
      let array = this.coinList.filter((coinItem) => {
        return coinItem.symbol
          .toUpperCase()
          .includes(this.inputAddValue.toUpperCase());
      });

      array.sort((a, b) => {
        const aSymbol = a.symbol.toUpperCase();
        const bSymbol = b.symbol.toUpperCase();
        const inputAddValue = this.inputAddValue.toUpperCase();

        if (aSymbol === inputAddValue && bSymbol !== inputAddValue) {
          return -1;
        } else if (aSymbol !== inputAddValue && bSymbol === inputAddValue) {
          return 1;
        } else {
          return 0;
        }
      });

      if (this.inputAddValue.length === 0) {
        array = this.customCoinList;
      } else {
        array = array.slice(0, 4);
      }

      return array;
    },
  },
  beforeCreate() {},
  created() {
    this.setCoinList();
  },
  beforeMount() {},
  mounted() {
    showButtonClickWave();
  },
  beforeUpdate() {},
  updated() {},
  errorCaptured() {},
};
</script>

<style lang="scss">
.error-messages {
  height: 20px;
}
</style>
