const apiKey =
  "80170320d4bb2b1d9e6e9c84246ba27005aa3e6d3e245edefc23ee99a653fc60";

async function getCoinPrice(obj) {
  const f = await fetch(
    `https://min-api.cryptocompare.com/data/price?fsym=${obj.name}&tsyms=USD&api_key=${apiKey}`
  );
  const respData = await f.json();
  return respData;
}

export {
  getCoinPrice
};