<template>
  <section class="flex flex-col">
    <div v-show="tickers.length !== 0">
      <div class="flex items-center max-w-xs relative">
        <button
          v-show="inputFilterValue.length !== 0"
          @click="clearInputFilter"
          type="button"
          class="absolute top-2.2 right-2"
          style=""
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            xmlns:svgjs="http://svgjs.com/svgjs"
            version="1.1"
            width="20"
            height="20"
            x="0"
            y="0"
            viewBox="0 0 511.76 511.76"
            xml:space="preserve"
          >
            <g>
              <path
                d="M436.896,74.869c-99.84-99.819-262.208-99.819-362.048,0c-99.797,99.819-99.797,262.229,0,362.048    c49.92,49.899,115.477,74.837,181.035,74.837s131.093-24.939,181.013-74.837C536.715,337.099,536.715,174.688,436.896,74.869z     M361.461,331.317c8.341,8.341,8.341,21.824,0,30.165c-4.16,4.16-9.621,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    l-75.413-75.435l-75.392,75.413c-4.181,4.16-9.643,6.251-15.083,6.251c-5.461,0-10.923-2.091-15.083-6.251    c-8.341-8.341-8.341-21.845,0-30.165l75.392-75.413l-75.413-75.413c-8.341-8.341-8.341-21.845,0-30.165    c8.32-8.341,21.824-8.341,30.165,0l75.413,75.413l75.413-75.413c8.341-8.341,21.824-8.341,30.165,0    c8.341,8.32,8.341,21.824,0,30.165l-75.413,75.413L361.461,331.317z"
                fill="#15803D"
                data-original="#000000"
              ></path>
            </g>
          </svg>
        </button>
        <label class="block text-sm text-center font-medium text-gray-700 mr-2"
          >Filter</label
        ><input
          type="text"
          class="block w-full pr-10 border-gray-300 text-gray-900 focus:outline-none focus:ring-gray-500 focus:border-green-700 sm:text-sm rounded-md"
          placeholder="For example BTC"
          v-model="inputFilterValue"
          @input="filterTickets()"
        />
      </div>
      <hr class="w-full border-t border-gray-200 my-4" />
      <div
        v-show="showFilterMessage"
        class="block text-sm font-medium text-gray-700 mt-8"
      >
        Nothing found
      </div>
      <dl class="mt-5 grid grid-cols-2 gap-2 sm:grid-cols-3">
        <div
          v-for="(ticker, tickerKey) in tickers"
          :key="tickerKey"
          :class="{
            'current-ticker': sell === ticker && showGraph === true,
            'no-price': ticker.price ? false : true,
          }"
          :data-current="sell === ticker"
          class="ticker show border-slate-200 bg-white overflow-hidden shadow rounded-lg border-solid cursor-pointer border-2 hover:border-green-700"
        >
          <div
            @click="select(ticker)"
            class="ticker-info px-2 py-2 sm:p-6 text-center flex justify-center align-middle table-column"
          >
            <dt class="text-sm font-medium text-gray-500 truncate">
              <span class="ticker-name inner-name">{{ ticker.name }}</span> -
              USD
            </dt>
            <dd class="mt-1 text-3xl font-semibold text-gray-900">
              {{ ticker.price ? ticker.price : "No coin value" }}
            </dd>
            <dd class="mt-1 text-sm font-medium text-gray-500 truncate">
              <span class="inner-name">{{
                ticker.fullName
                  ? ticker.fullName
                  : "Coin " + "(" + ticker.name + ")"
              }}</span>
            </dd>
          </div>
          <!-- <div class="w-full border-t border-gray-200"></div> -->
          <!-- @click.stop="remove(tickerKey, $event)" -->
          <button
            @click="rerenderCurrentTickers()"
            @click.stop="tickerData(ticker.fullName, tickerKey, $event)"
            class="ticker-button flex items-center justify-center font-medium w-full px-4 py-1 sm:px-6 border-t-2 border-slate-200 text-md text-gray-500 hover:bg-green-700 hover:text-white transition-all focus:outline-none"
          >
            Delete
          </button>
        </div>
      </dl>
      <hr class="w-full border-t border-gray-200 my-4" />
    </div>
    <pagination
      v-model="currentPage"
      :records="totalItems"
      :per-page="itemsOnThePage"
    />
  </section>
</template>

<script>
import Pagination from "v-pagination-3";

export default {
  components: { Pagination },
  props: {
    tickers: { type: Array, required: true, default: () => [] },
    sell: { type: Object, required: false, default: null },
    showGraph: { type: Boolean, required: true, default: false },
  },
  emits: {
    tickerId: { type: Number },
    storageTickers: { type: Array },
    newSell: { type: Object },
    newShowGraph: { type: Boolean },
    tickerForSubscribe: { type: Object },
    newTickers: { type: Array },
    newGraphData: null,
    newObjForSell: null,
    changeStatus: null,
    getTickerData: null,
  },
  data() {
    return {
      // Tickers
      currentTickers: [],

      // Tickers - Filter
      inputFilterValue: "",
      showFilterMessage: false,

      // Pagination
      currentPage: 1,
      itemsOnThePage: 6,
      totalItems: 0,

      // For sell
      objForSell: null,
    };
  },
  computed: {
    computedTickersLength() {
      return this.tickers.length;
    },
  },
  watch: {
    computedTickersLength() {
      this.rerenderCurrentTickers();
      this.setStorageTickets();
    },
    tickers(newValue) {
      if (this.sell !== null) {
        const sellTicker = newValue.find((ticker) => {
          return ticker.name === this.sell.name;
        });
        this.objForSell = sellTicker;
      }
    },
    objForSell(newValue) {
      this.rerenderCurrentTickers();
      if (
        this.sell !== null &&
        this.objForSell !== null &&
        this.sell.price !== newValue.price
      ) {
        this.$emit("newSell", newValue);
      }
    },
  },
  methods: {
    tickerData(tickerName, tickerKey, $event) {
      this.$emit("changeStatus");
      this.$emit("getTickerData", tickerName, tickerKey, $event);
    },
    select(ticker) {
      this.inputFilterValue = "";
      this.$emit("newGraphData");
      this.$emit("newSell", ticker);
      this.$emit("newShowGraph", true);
      this.rerenderCurrentTickers();
    },
    filterTickets() {
      const tickers = document.querySelectorAll(".ticker");

      if (this.inputFilterValue.length > 0) {
        tickers.forEach((ticker) => {
          const coinName = ticker.querySelector(".ticker-name");

          if (
            coinName.innerHTML
              .toUpperCase()
              .includes(this.inputFilterValue.toUpperCase())
          ) {
            coinName.closest(".ticker").classList.remove("hidden");
          } else {
            coinName.closest(".ticker").classList.add("hidden");
          }

          let allTickersHidden = true;

          tickers.forEach((ticker) => {
            if (!ticker.classList.contains("hidden")) {
              allTickersHidden = false;
              return;
            }
          });

          if (allTickersHidden) {
            this.showFilterMessage = true;
          } else {
            this.showFilterMessage = false;
          }
        });

        this.totalItems = 0;
      } else {
        this.inputFilterValue = "";

        tickers.forEach((ticker) => {
          ticker.classList.remove("hidden");
        });

        this.showFilterMessage = false;
      }
    },
    clearInputFilter() {
      this.inputFilterValue = "";
      this.showFilterMessage = false;

      const tickers = document.querySelectorAll(".ticker");
      tickers.forEach((ticker) => {
        ticker.classList.remove("hidden");
      });
    },
    rerenderCurrentTickers() {
      this.$nextTick(() => {
        const tickerNames = document.querySelectorAll(".ticker-name");
        tickerNames.forEach((tickerName) => {
          if (this.sell !== null && tickerName.innerHTML === this.sell.name) {
            tickerName.closest(".ticker").classList.add("current-ticker");
          } else {
            tickerName.closest(".ticker").classList.remove("current-ticker");
          }
        });
      });
    },
    remove(tickerKey, event) {
      if (event.target.closest(".current-ticker")) {
        this.$emit("newShowGraph", false);
        this.$emit("newSell", null);
        this.$emit("newObjForSell", null);
      }
      this.$emit("tickerId", tickerKey);

      if (
        this.tickers.length % 6 === 0 &&
        this.totalItems !== 0 &&
        this.currentPage !== 1
      ) {
        this.currentPage = this.currentPage - 1;
      }

      this.inputFilterValue = "";
    },
    showOnlyCurrentTickers() {
      const currentTickersHTML = document.querySelectorAll(".ticker");
      this.currentTickers = currentTickersHTML;
      this.totalItems = this.currentTickers.length;

      this.currentTickers.forEach((ticker, index) => {
        ticker.style.display = this.isTickerVisible(index) ? "flex" : "none";
      });
    },
    isTickerVisible(index) {
      const startIndex = (this.currentPage - 1) * this.itemsOnThePage;
      const endIndex = startIndex + this.itemsOnThePage;
      return index >= startIndex && index < endIndex;
    },
    setStorageTickets() {
      localStorage.setItem("tickers", JSON.stringify(this.tickers));
    },
    setTickersFromStorage() {
      if (JSON.parse(localStorage.getItem("tickers")) !== null) {
        const tickersFromStorage = JSON.parse(localStorage.getItem("tickers"));
        this.$emit("newTickers", tickersFromStorage);
      }
    },
    subscribeToPrice() {
      this.tickers.forEach((ticker) => {
        this.$emit("tickerForSubscribe", ticker);
      });
    },
    setTotalItemsLength() {
      this.currentTickers = document.querySelectorAll(".ticker");
      this.totalItems = this.currentTickers.length;
    },
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    this.setTotalItemsLength();
    this.setTickersFromStorage();
    this.subscribeToPrice();
  },
  beforeUpdate() {},
  updated() {
    this.$nextTick(this.showOnlyCurrentTickers());
  },
};
</script>

